.promo {
  background-color: transparent;
  color: $white;
  position: relative;
  overflow: hidden;

  // &::before {
  //   content: "";
  //   width: 100%;
  //   height: 950px;
  //   position: absolute;
  //   top: -120px;
  //   border: 3px solid green;
  //   background-image: url("../img/background-main@1x.png");
  //   background-size: 100%;
  // }

  &__wrapper {
    @include container-desktop-wide;
    padding-top: 200px;
    padding-bottom: 278px;
    position: relative;
    z-index: 3;
  }

  h2 {
    margin: 0;
    font-size: 80px;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
  }

  p {
    font-family: $pt-mono;
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 34px;
    padding-left: 92px;
    box-sizing: inherit;
    position: relative;
    margin-top: 25px;
    margin-bottom: 45px;
    display: block;
    width: 55%;
  }

  p::before {
    content: "";
    position: absolute;
    width: 66px;
    height: 2px;
    background-color: $white;
    left: 0;
    top: 15px;
  }
}

.promo__button,
.promo__button--mobile {
  box-sizing: border-box;
  display: inline-block;
  height: 55px;
  background-color: $blue;
  font-size: 16px;
  line-height: 35px;
  letter-spacing: 0.05em;
  width: 407px; // временно
  margin-left: 92px;
}

.promo__button--mobile {
  display: none;
}

.promo__scroll-link {
  position: absolute;
  transform: rotate(-90deg);
  padding: 5px;
  left: -20px;
  bottom: 125px;
  font-family: $pt-mono;
  font-size: 14px;
  line-height: 16px;
  color: $white;
  opacity: 0.5;
}

.promo__image {
  position: absolute;
  width: 50%;
  left: 100%;
  margin-left: -46%;
  top: 140px;
  background-image: url("../img/triangles@1x.png");
  background-repeat: no-repeat;
  background-position: -5px -140px;
}

@media (max-width: 1023px) {
  .promo {
    background-color: $dark-grey;

    h2 {
      font-size: 60px;
      line-height: 70px;
      margin-right: 50px;
    }

    p {
      font-size: 18px;
      line-height: 28px;
      width: 70%;
      padding-left: 50px;
      margin-bottom: 55px;
    }

    p::before {
      width: 33px;
    }
  }

  .promo__scroll-link {
    display: none;
  }

  .promo__wrapper {
    padding: 90px 35px 165px 25px;
    width: 83%;
  }

  .promo__button {
    width: 379px;
    margin-left: 47px;
  }

  .promo__image {
    top: 10px;
    margin-left: -34%;
    background-image: none;

    img {
      width: 465px;
      height: auto;
    }

    // &::before {
    //   content: "";
    //   position: absolute;
    //   width: 530px;
    //   height: 506px;
    //   border: 1px solid green;
    //   background-image: url("../img/triangles@1x.png");
    //   background-position: top left;
    //   z-index: 3;
    // }
  }
}

@media (max-width: 767px) {
  .promo {
    h2 {
      font-size: 40px;
      line-height: 47px;
    }

    p {
      font-size: 16px;
      line-height: 25px;
      width: 100%;
      margin-bottom: 45px;
    }
  }

  .promo__wrapper {
    @include container-mobile;
    width: 100%;
    padding-top: 195px;
    padding-bottom: 70px;
  }

  .promo__image {
    left: 42%;
    top: 0;

    img {
      width: 301px;
      height: auto;
    }
  }

  .promo__button {
    display: none;
  }

  .promo__button--mobile {
    margin: 0 auto;
    display: block;
    max-width: 450px;
    width: 100%;
  }
}
